import React from "react"
import VisibilitySensor from "../react-visibility-sensor-once"
import { Parallax } from "react-scroll-parallax"

import Style from "./bodyCopy.module.scss"

const BodyCopy = ({ anchor, classes, title, children }) => (
  <VisibilitySensor minTopValue={"200"} partialVisibility once>
    {({ isVisible }) => (
      <section
        id={anchor}
        className={classes + " animUp" + (isVisible ? " in" : " off")}
      >
        <div className="gridContainer">
          <div className="grid-3-center">
            <div className="col-12">
              <Parallax y={[-20, 22]}>
                <h1 className={Style.backgroundTitle}>{title}</h1>
              </Parallax>
            </div>
            <div className="col-12_md-10">
              <h5>{title}</h5>
              {children}
            </div>
          </div>
        </div>
      </section>
    )}
  </VisibilitySensor>
)

export default BodyCopy
